.aside {
  @media (min-width: $large) {
    margin-right: 3.2em;
  }

  .food-sense-logo {
    align-self: center;
  }

  .container {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;

    @media (min-width: $small) {
      flex-direction: row;
      justify-content: space-between;
      margin-top: 1.5em;
    }

    @media (min-width: $large) {
      flex-direction: column;
      align-items: flex-start;
      margin-top: 0;
    }
  }
}

.nav {
  margin: 2em 0;

  @media (min-width: $large) {
    margin: 0;
    margin-bottom: 1.5em;
  }
}

.nav-list {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $large) {
    flex-direction: column;
  }
}

.nav-link {
  margin: 0 0.9em;
  color: $primary;
  font-style: italic;
  text-decoration: none;

  @media (min-width: $medium) {
    margin: 0 1.5em;
  }
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $large) {
    flex-direction: row;
    margin-top: 2em;

    &:first-child {
      margin-top: 1em;
    }
  }
}

.nav-icon {
  display: none;

  @media (min-width: $medium) {
    display: block;
    margin-bottom: 0.5em;
  }
}

.aside-tweet {
  display: none;

  @media (min-width: $large) {
    display: block;
  }
}

.aside-header {
  margin-bottom: 0;
  font-weight: 400;
  font-size: 1.2rem;
  font-family: $verdana-font;
  text-transform: uppercase;
  border-bottom: 1px dashed $smokie;
}

.aside-tweet-text {
  max-width: 140px;
  font-size: 1rem;
  font-style: italic;
}
